class InputField {
  constructor(input) {
    this.input = input;
    this.parent = input.parentElement;
  }

  static init(domElements = []) {
    domElements.forEach((element, i) => {
      console.log(element);
      if (element.getAttribute("type") == "password")
        new PasswordField(element);
      else new InputField(element);
    });
  }
}

class PasswordField extends InputField {
  isVisible = false;

  constructor(input) {
    super(input);
    this.toggleButton = this.parent.querySelector(".field-password__toggle");
    this.toggleButton.addEventListener("click", this.onClick);
  }

  onClick = () => {
    this.isVisible = !this.isVisible;
    this.toggleVisibility();
  };

  toggleVisibility = () => {
    this.toggleButton.classList.toggle("active");
    this.toggleButton.innerText = this.isVisible ? "hide" : "show";
    this.input.setAttribute("type", this.isVisible ? "text" : "password");
  };
}

export default InputField;
